import React, { Component } from 'react';
import styled from 'styled-components';

import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';

const Nav = styled.div`
    display: flex;
    flex-direction:row;
    flex-flow: wrap;
    justify-content: flex-end;
    align-items:center;
    background:white;
    margin-bottom:1rem;

    * > {
        margin-right:10px;
        background:red;
    }

    input[type=text]{
        padding: 12px 20px;
        margin: 8px 0;
        border: 1px solid #ccc;
        border-radius: 4px;
        box-sizing: border-box;
    }

    >*:first-child {
        margin-right:auto;
    }
    
    >* {
        margin-right:20px;
    }

    @media (max-width: 620px) {
    flex-direction: column;
  }
`

const Logo = styled.h1`
    margin-left:2rem;
`

const SearchButton = styled.i`
    margin-right:2rem;
`
class NavBar extends Component {

    constructor(props) {
        super(props);
        this.state = {
            parentState: props.parent,
            location: '',
            radioOption: 'followers'
        };

        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
        this.radioChange = this.radioChange.bind(this)
    }


    handleChange(event) {
        this.setState({ location: event.target.value });
    }

    /**
     * Handles the form submission
     */
    handleSubmit(event) {
        event.preventDefault();
        this.props.onChange(this.state.location,this.state.radioOption)
    }


    radioChange(event) {
        this.setState({ radioOption: event.target.value });
    }

    render() {
        return (
            <form onSubmit={this.handleSubmit}>
                <Nav>
                    <Logo>GitScout</Logo>
                    <label>
                        <span className="filterBy">Filter by:</span>
                        <input type="text" id="location" name="location" placeholder="Wellington" value={this.state.location} onChange={this.handleChange} />
                    </label>

                    <select id="Language" name="lang">
                        <option value="australia">Java</option>
                        <option value="canada">JavaScript</option>
                        <option value="usa">Python</option>
                    </select>

                    <RadioGroup
                        aria-label="Gender"
                        name="gender1"
                        className="radioGroup"
                        style={{ flexDirection: 'row' }}
                        onChange = {this.radioChange}
                    >
                        <FormControlLabel value="followers" control={<Radio color="default" />} label="Followers" />
                        <FormControlLabel value="repositories" control={<Radio color="default" />} label="Repositories" />
                        {/* <FormControlLabel value="gists" control={<Radio color="default" />} label="Gists" /> */}
                    </RadioGroup>
                    {/* <input type="submit" value="Submit"></input> */}
                    <SearchButton className="fas fa-search"></SearchButton>
                </Nav>
            </form >

        );
    }
}

export default NavBar;